<template>
  <div class="like">
    <van-nav-bar title="我的收藏" left-text="返回" left-arrow :fixed="true" @click-left="back" />
    <div class="like-bg"></div>
    <div class="like-box">
      <div class="product-box clearfix" v-if="likeData.length > 0">
        <div class="product-item fl" v-for="(item, index) in likeData" :key="index">
          <div class="tag">
            {{index+1}}
            <div class="kong"></div>
          </div>
          <van-card origin-price="36.00">
            <template #thumb>
              <van-tag plain>
                <img class="auto-img" :src="item.smallImg" alt @click="lookInfo(item.pid)" />
              </van-tag>
            </template>
            <template #title>
              <van-tag plain>
                <div class="title">{{item.name}}</div>
                <div class="desc">{{item.enname}}</div>
              </van-tag>
            </template>
            <template #price>
              <van-tag plain>
                <div class="price">{{item.price}}</div>
              </van-tag>
            </template>
            <template #num>
              <van-tag plain>
                <div class="num">
                  <img src="../assets/images/delete.png" alt class="auto-img" @click="removemyLike(item.pid, index)" />
                </div>
              </van-tag>
            </template>
          </van-card>
        </div>
      </div>
      <div v-else>
        <van-empty description="收藏商品空空如也，去逛一逛!">
          <van-button round color="#5f56ff" class="bottom-button" @click="goMenu">去逛一逛</van-button>
        </van-empty>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/like.less";

import { utils } from "../assets/js/utils";

export default {
  data() {
    return {
      //我的收藏商品数据
      likeData: [],
    };
  },

  created() {
    this.getmyLike();
  },

  methods: {
    goMenu() {
      return this.$router.push({ name: "Menu" });
    },
    back() {
      this.$router.go(-1);
    },

    //获取我的收藏商品数据
    getmyLike() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllLike",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 2000) {
            this.likeData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //查看商品详情
    lookInfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },

    //删除收藏商品数据
    removemyLike(pid, index) {
      //pid: 商品id
      //index: 下标

      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/notlike",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 700) {
            return this.$router.push({ name: "Login" });
          }

          if (result.data.code == 900) {
            this.likeData.splice(index, 1);
            this.$toast("删除收藏商品成功");
          } else {
            this.$toast("删除收藏商品失败");
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>